@import "../../style/variables";

.best-product {
  padding: 80px 0 110px 0;
  background: url("../../resources/img/best_product.jpg") center center/cover;
  &__coffee-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}

.best-coffee-item {
  margin: 39px 35px 0 35px;
  padding: 22px 20px;
  width: 220px;
  background: rgba(255, 255, 255, 0.65);
  border-radius: 8px;
  &__img {
    width: 180px;
    height: 175px;
    object-fit: contain;
  }
  &__title {
    margin-top: 10px;
    overflow: hidden;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: end;
    color: $text-color;
  }
  &__price {
    margin-top: 10px;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    text-align: end;
    color: $text-color;
  }
}
